const config = require("../config/index");
const fsUtils = require("../utils/fsUtils");
const path = require("path");
const vueTemplateCompiler = require("vue-template-compiler");
const recast = require("recast");
const babelParser = require("recast/parsers/babel");
const Resolve = require("enhanced-resolve");

let resolve = null;

/**
 *  js / vue
 * @param { String } _absPath
 * @param { String } _ext
 */
async function getDependency(_absPath, _ext) {
  try {
    const workspacePath = fsUtils.getWorkspacePath(); // 
    let importList = [];
    const content = await fsUtils.readFile(_absPath); // 
    const codeString = content.toString();

    if (_ext === ".vue") {
      importList = vueParser(codeString); //  vue
    }
    if (_ext === ".js") {
      importList = jsParser(codeString);
    }

    // 
    const dependencies = importList.reduce((res, item) => {
      const itemAbsPath = resolvePath(item, _absPath);
      const ext = path.extname(_absPath).toLowerCase();

      if (item && itemAbsPath) {
        // 
        if (fsUtils.checkFileType(ext)) {
          // 
          if (!itemAbsPath.includes("node_modules")) {
            // 
            if (fsUtils.checkInEntry(itemAbsPath)) {
              res.push(itemAbsPath.replace(workspacePath, ""));
            }
          }
        }
      }

      return res;
    }, []);

    return dependencies;
  } catch (err) {
    console.log(err.message);
    console.log(_absPath);
  }

  return [];
}

/**
 *  vue 
 * @param { String } _codeString
 * @todo  nuxt  import 
 */
function vueParser(_codeString) {
  try {
    const compileResult = vueTemplateCompiler.parseComponent(_codeString); //  vue
    const scriptString = compileResult?.script?.content || ""; //  vue  js 

    const dependencies = jsParser(scriptString);

    return dependencies;
  } catch (err) {
    throw new Error("vue ");
  }
}

/**
 * JS
 * @param { String } _codeString JS
 */
function jsParser(_codeString) {
  const dependencies = [];

  try {
    const ast = babelParser.parse(_codeString, {}); // js 

    recast.visit(ast, {
      //  js  import  import 
      visitImportDeclaration(nodePath) {
        try {
          const importPath = nodePath?.node?.source?.value || "";

          if (importPath) dependencies.push(importPath);
        } catch (err) {
          console.log(" js ");
        }

        return false;
      },

      //  import()
      visitCallExpression(nodePath) {
        try {
          const node = nodePath.value;

          if (
            node?.callee?.type === "Import" ||
            node?.callee?.name === "require"
          ) {
            if (
              node.arguments?.length &&
              node.arguments[0]?.type === "StringLiteral"
            ) {
              const importPath = node.arguments[0]?.value || "";

              if (importPath) dependencies.push(importPath);
            }
          }
        } catch (err) {
          console.log(" js ");
        }

        return false;
      },
    });

    return dependencies;
  } catch (err) {
    throw new Error("js ");
  }
}

/**
 *  import 
 * @param { String } _path import  import 
 * @param {String} _absPath 
 */
function resolvePath(_importPath, _absPath) {
  const workspacePath = fsUtils.getWorkspacePath(); // 

  if (!resolve) {
    const alias = {};

    //  alias 
    for (const item in config.alias) {
      alias[item] = path.resolve(workspacePath, config.alias[item]);
    }

    resolve = Resolve.create.sync({
      extensions: [".vue", ".js"],
      alias: alias,
    });
  }

  try {
    const dirName = path.dirname(_absPath); // 
    const importAbsPath = resolve(dirName, _importPath);

    // return importAbsPath.replace(workspacePath, "");
    return importAbsPath;
  } catch (err) {
    console.log(`${_importPath}`);
  }

  return "";
}

module.exports = {
  getDependency,
};
